@import url('https://fonts.googleapis.com/css2?family=Corben:wght@400;700&family=Tajawal:wght@300;400;700;800;900&display=swap');

html {
    scroll-behavior: smooth;
}
body {
    margin: 0;
    padding: 0 16rem;
    font-size: 20px;
    background-color: #ffffff;
    color: #505050;
    font-family: 'Tajawal', sans-serif;
    font-weight: 300;
}
@media (max-width: 420px) {
    body {
        padding: 0 1rem;
    }
}
@media (min-width:421px) and ( max-width:900px) {
    body {
        padding: 0 2rem;
    }
}

a {
    color: #ff7575;
    text-decoration: none;
}
a:hover {
    text-decoration: underline;
}

#header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.6rem;
    flex: 1 1 auto;
    flex-wrap: wrap;
}
#header h1 {
    font-family: 'Corben', sans-serif;
    font-size: 1.8rem;
}
#header ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.8rem;
}
#header a {
    color: #505050;
}
#header a.current {
    text-decoration: underline #ff7575;
}

h2 {
    font-family: 'Corben', sans-serif;
    font-weight: 400;
    font-size: 1.4rem;
}
.content {
    margin-bottom: 3rem;
}
.posttitle,
.pagetitle {
    text-align: center;
    font-size: 2rem;
}
.date,
.author {
    font-size: 1rem;
}
.pagination {
    margin: 4rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1 1 auto;
    flex-wrap: wrap;
}

.postNavigation {
    margin: 4rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1 1 auto;
    flex-wrap: wrap;
}
.prevlink,
.nextlink {
    max-width: 20rem;
}
.nextlink {
    text-align: right;
}

#footer {
    padding: 4rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1 1 auto;
    gap: 3rem;
    flex-wrap: wrap;
}
#footer ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.2rem;
    font-size: 1.8rem;
}